@import url(https://fonts.googleapis.com/css?family=Muli:400,600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

.segment {
  margin: auto;
  padding-bottom: 30px;
  max-width: 880px; }
  .segment--first {
    padding-top: 30px; }
  .segment__text {
    font-size: 18px;
    max-width: 36em;
    margin: 30px auto;
    font-weight: 600;
    line-height: 1.44; }
  .segment__links {
    color: #f1ad03;
    font-size: 18px;
    font-weight: 600; }
  .segment__link {
    -webkit-transition: color 300ms ease-in-out;
    transition: color 300ms ease-in-out;
    text-decoration: none;
    color: #f1ad03;
    margin-right: 58px; }
    .segment__link:hover {
      color: rgba(241, 173, 3, 0.6); }
    @media (max-width: 767px) {
      .segment__link {
        display: block;
        margin: auto;
        margin-bottom: 40px; } }
    .segment__link--last {
      margin-right: 0; }
      @media (max-width: 767px) {
        .segment__link--last {
          margin-bottom: 0; } }

img.logo {
  max-height: 357px; }

.social {
  margin: auto;
  margin-top: 30px;
  display: inline-block; }
  @media (max-width: 767px) {
    .social {
      display: flex;
      justify-content: space-evenly;
      margin-top: 10px; } }
  .social__wrapper {
    width: 28px;
    height: 28px;
    box-sizing: content-box;
    display: inline-block;
    color: #b7e4cf;
    margin: 0 4px;
    opacity: 1;
    -webkit-transition: opacity 170ms ease-in-out;
    transition: opacity 170ms ease-in-out; }
    .social__wrapper--dim {
      opacity: 0.4; }
    .social__wrapper:hover {
      opacity: 1; }
  .social__icon {
    line-height: 28px;
    vertical-align: bottom;
    font-size: 18px;
    height: 18px; }

.App {
  text-align: center;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  font-family: 'Muli', sans-serif;
  text-rendering: optimizeLegibility;
  color: white;
  letter-spacing: 0em; }
  @media (max-width: 767px) {
    .App {
      overflow: scroll;
      height: auto;
      width: 100%;
      display: flex;
      flex-direction: column-reverse; } }

img.background {
  height: 100%;
  position: fixed; }
  @media (max-width: 767px) {
    img.background {
      position: relative;
      height: auto;
      left: -20%;
      width: 140%; } }

.content {
  height: 100%;
  position: absolute;
  background-color: #232444;
  width: 50%;
  min-width: 700px;
  overflow-y: scroll; }
  @media (max-width: 767px) {
    .content {
      height: auto;
      min-width: unset;
      overflow: hidden;
      width: 100%;
      position: relative; } }
  .content .container {
    padding: 25px 60px;
    position: relative;
    box-sizing: border-box;
    height: auto; }
    @media (max-width: 767px) {
      .content .container {
        padding: 25px 30px; } }

h1 {
  font-size: 40px;
  font-weight: normal;
  text-align: left;
  margin-bottom: 5px; }

.vol__info {
  text-align: left;
  font-weight: normal;
  line-height: 1.44;
  font-size: 18px;
  margin-top: 7px; }
  .vol__info--top {
    margin-top: 30px; }

.vol__form {
  padding: 40px; }
  @media (max-width: 767px) {
    .vol__form {
      padding: 40px 0; } }

.vol__check {
  text-align: left;
  margin-bottom: 30px; }
  .vol__check input {
    position: absolute;
    z-index: 999;
    opacity: 0;
    width: 18px;
    height: 18px;
    cursor: pointer; }
    .vol__check input:checked + .vol__check__box:after {
      visibility: visible; }
  .vol__check__label {
    vertical-align: middle;
    font-size: 18px;
    font-weight: 400;
    line-height: 1.44;
    cursor: pointer; }
    .vol__check__label svg {
      font-size: 12px;
      vertical-align: middle;
      margin-left: 10px; }
  .vol__check__box {
    position: relative;
    height: 18px;
    width: 18px;
    border: 2px solid #b7e4cf;
    display: inline-block;
    border-radius: 2px;
    margin-right: 30px;
    vertical-align: middle;
    cursor: pointer; }
    .vol__check__box:after {
      content: "";
      position: absolute;
      visibility: hidden;
      left: 5px;
      top: -8px;
      width: 8px;
      height: 18px;
      border: solid white;
      border-width: 0 4px 4px 0;
      -webkit-transform: rotate(40deg);
              transform: rotate(40deg); }
  .vol__check__desc {
    padding-top: 30px;
    font-size: 14px;
    line-height: 1.43; }
    .vol__check__desc ul {
      line-height: 2; }

.vol__error {
  visibility: hidden;
  text-align: right;
  color: #b61b28;
  font-size: 14px;
  font-weight: 600; }

.vol__inputs {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding-top: 10px; }
  @media (max-width: 767px) {
    .vol__inputs {
      margin-bottom: 50px; } }

.vol__input-container {
  text-align: left;
  width: 47%;
  margin-bottom: 15px; }
  .vol__input-container label {
    display: block;
    padding-bottom: 8px; }
  .vol__input-container--full {
    width: 100%; }
  @media (max-width: 767px) {
    .vol__input-container {
      width: 100%; } }

.vol__text-input {
  border: solid 2px white;
  background-color: #283052;
  color: white;
  font-size: 18px;
  line-height: 1.44;
  padding: 7px 15px;
  border-radius: 1px;
  box-sizing: border-box;
  width: 100%; }
  .vol__text-input::-webkit-input-placeholder {
    color: white;
    opacity: 0.4; }
  .vol__text-input::-moz-placeholder {
    color: white;
    opacity: 0.4; }
  .vol__text-input:-ms-input-placeholder {
    color: white;
    opacity: 0.4; }
  .vol__text-input::-ms-input-placeholder {
    color: white;
    opacity: 0.4; }
  .vol__text-input::placeholder {
    color: white;
    opacity: 0.4; }
  .vol__text-input:focus {
    border-color: #b7e4cf;
    outline: none; }
  .vol__text-input--required:valid:not(:focus) {
    background-image: url(/static/media/check.42a32373.svg);
    background-repeat: no-repeat;
    background-position: right;
    background-origin: content-box; }
  .vol__text-input--visited:invalid {
    border-color: #b61b28;
    background-image: url(data:image/png;base64,);
    background-repeat: no-repeat;
    background-position: right;
    background-origin: content-box;
    background-size: 24px 16px; }
    .vol__text-input--visited:invalid + div.vol__error {
      visibility: visible; }

.vol__submit {
  margin: auto; }

.vol__done {
  color: #f1ad03;
  font-size: 18px;
  line-height: 1.44;
  margin-top: 30px;
  display: block; }

.vol__flash {
  position: fixed;
  top: 0;
  color: white;
  background-color: #b61b28;
  width: 100%;
  padding: 10px;
  left: 0;
  z-index: 1; }

.button {
  width: 265px;
  background-color: #f1ad03;
  color: #232444;
  font-size: 18px;
  font-weight: 600;
  border: unset;
  border-radius: 1px;
  cursor: pointer;
  -webkit-transition: background-color 300ms ease-in-out;
  transition: background-color 300ms ease-in-out;
  display: block;
  text-decoration: none;
  padding: 15px 0;
  line-height: 1.44; }
  .button:hover {
    background-color: rgba(241, 173, 3, 0.6); }

.close-link {
  position: absolute;
  color: #f1ad03;
  font-size: 36px;
  text-decoration: none;
  right: 30px;
  top: 15px;
  font-family: sans-serif;
  font-weight: 100;
  -webkit-transition: color 300ms ease-in-out;
  transition: color 300ms ease-in-out; }
  .close-link:hover {
    color: rgba(241, 173, 3, 0.6); }

.training__container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  padding-top: 10px;
  margin-top: 50px;
  margin-left: -5px;
  margin-right: -5px; }

.training__button {
  display: inline-block;
  margin-bottom: 20px;
  padding: 20px 0;
  margin-left: 10px;
  margin-right: 10px;
  border-radius: 1px; }


